* {
    padding: 0;
    margin:  0;
}

.App {
    overflow-x: hidden;
    display: grid;
    justify-content: center;
    justify-items: center;
    align-content: start;
    --sunset: #fca644;
    --silhouette: #202036;
    font-family: "Readex Pro", sans-serif;
    background: rgb(253,62,45);
    background: linear-gradient(0deg, rgba(253,62,45,1) -15%, rgba(233,143,59,1) 33%, rgba(183,218,216,1) 65%, rgba(34,193,195,1) 110%);
    width: 100%;
    height: 100vh;
    grid-template-rows: auto 1fr 0;
    overflow-y: hidden;
}

h1 {
    font-family: 'Pacifico', cursive, serif;
    color: var(--silhouette);
}

h1#title {
    font-size: 2.7rem;
    padding-top: 2rem;
}

#links {
    margin-top: 2rem;
    display: grid;
    grid-gap: 1rem;
    justify-items: center;
    align-content: start;
}

#links a {
    font-size: 2rem;
    color: var(--silhouette);
    text-decoration: none;
}

.center-text {
    display: grid;
    justify-items: center;
    text-align: center;
    margin-top: 1rem;
    font-size: 1.32rem;
}

a {
    text-decoration: none;
}

.hills {
    width: calc( (100vw + 150rem) / 2 );
    min-width: 100vw;
    transform: translateY( calc( (100vw + 150rem) / -25 ) );
    justify-self: left;
    overflow: visible;
}